.container-row
    min-height: 80vh
    padding: 50px 0
    width: 100%
    &:nth-child(odd)
        background-color: #f7f7f7
    &:nth-child(even)
        background: #fff
    .ant-space-item
        margin: 0 auto
        width: 80%
        text-align: center
        .ant-typography
            margin: 0 auto
            width: 60%