@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300400&display=swap);
.logo{align-items:center;display:flex;height:30px;justify-content:center;width:50px}header{align-items:center;display:flex;justify-content:space-between;position:fixed;width:100%;z-index:1}header .logo{background:rgba(255,255,255,.2);color:#fff}footer{display:flex;flex-flow:wrap;justify-content:center}footer .ant-row{align-items:center;text-align:center;width:60%}footer .ant-row .logo{background:rgba(0,0,0,.2);color:#000}footer .ant-divider{border-top-color:#cdcdcd;min-width:60%;width:60%}
.container-row{min-height:80vh;padding:50px 0;width:100%}.container-row:nth-child(odd){background-color:#f7f7f7}.container-row:nth-child(even){background:#fff}.container-row .ant-space-item{margin:0 auto;width:80%;text-align:center}.container-row .ant-space-item .ant-typography{margin:0 auto;width:60%}
